.login {
    background-color: hsl(219, 48%, 8%);
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.loginContainer {
    width: 60%;
    border-radius: 12px;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.336);
}

.loginForm {
    background-color: inherit;
    border-radius: 12px;
    height: 55vh;
    /* padding: 2vmax; */
}

.loginForm>h4 {
    margin: 2vmax;
    letter-spacing: 15px;
    display: flex;
    transform: translateX(-100vw);
    animation: contactFormInputs 1s ease-in-out forwards;
    justify-content: center;
}

.loginForm>h4>p {
    transition: all 0.5s;
    cursor: default;
}

.loginForm>h4>p:hover {
    transform: translateY(-10px);
}

.loginForm>div {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 2vmax 8vmax;
    box-sizing: border-box;
}

.loginForm>div>input {
    padding: 1vmax 2vmax;
    margin: 1vmax;
    border: 1px solid rgba(0, 0, 0, 0.192);
    transform: translateX(-100vw);
    border-radius: 20px;
    animation: contactFormInputs 1s ease-in-out forwards 0.5s;
    outline: none;
    font: 100 1rem "Roboto";
}

.loginForm>div>button {
    padding: 1vmax 2vmax;
    margin: 1vmax;
    transform: translateY(-100vh);
    animation: contactFormBtn 1s ease-in-out forwards 1s;
    border-radius: 30px;
    background-color: hsl(250, 100%, 75%);
    color: white;
    transition: all 0.5s;
}

.loginForm>div>button:hover {
    background-color: hsl(219, 48%, 8%);
}

@media screen and (max-width: 600px) {
    .loginContainer {
        width: 90%;
        border-radius: 12px;
    }

    .loginForm {
        height: 30vh;
    }

    .loginForm>div {
        padding: 4vw 8vw;
    }

    .loginForm>div>input {
        padding: 3vw 6vw;
        margin: 2vw;
    }

    .loginForm>div>button {
        padding: 3vw 6vw;
        margin: 2vw;
    }
}

@keyframes contactTransition {
    from {
        transform: translateX(-100%) translateY(-50%);
    }

    to {
        transform: translateX(20%) translateY(-50%);
    }
}

@keyframes contactFormTransition {
    from {
        transform: translateY(-100%);
    }

    to {
        transform: translateY(0);
    }
}

@keyframes contactFormBtn {
    to {
        transform: translateY(0);
    }
}

@keyframes contactFormInputs {
    to {
        transform: translateX(0);
    }
}